import styled from 'styled-components';
import button_bg from "../../assets/images/button_bg.png";

export const ButtonDefault = styled.button`
    width: 217px;
    height: 68px;
    background: url(${button_bg}) no-repeat center;
    background-size: 100%;
    font-size: 30px;
    font-family: Source Han Sans CN;
    font-weight: bold;
    color: #FFFFFF;
    line-height: 34px;
    outline: none;
    cursor: pointer;
    z-index:199;
`

